const Info = (props) => {
    return (
        <div className={`flex mt-20 justify-between items-center w-full md:space-x-6 xl:space-x-10 flex-col ${props.orientation}`}>
            <div className='sm:w-1/3'>
                <img className='w-full' src={props.image_url} ></img>
            </div>
            <div className='sm:w-3/5'>
                <div className='xl:text-[24px] md:text-xl font-Questrial'>{props.description}</div>
                <div className='xl:text-[52px] text-5xl sm:mt-0 mt-10 font-Rockness text-beigePrimary'>{props.from}</div>
                <div className='xl:text-[48px] md:text-4xl font-Italiana text-xl'>{props.post}</div>
            </div>

        </div>
    )
}

export default Info