import git1 from "../assets/getInTouch1.png";
import git2 from "../assets/getInTouch2.png";
import PrimaryButton from "../components/PrimaryButton";
import formbg from "../assets/form-bg.png";

export function Form() {
    return <div className=" shadow-xl bg-cover bg-bottom sm:flex space-x-10 mx-7 sm:mx-[108px] absolute md:-mt-20 xl:-mt-40 z-10" style={{ "box-shadow": "-5px 0px 8px rgba(0, 0, 0, 0.25), 3px 6px 7px rgba(0, 0, 0, 0.25);", "background-image": `url(${formbg})` }}>
        <div className="xl:w-1/2 md:w-2/3 w-full flex flex-col items-center justify-center h-full">
            <div className="flex w-full flex-col xl:p-12 md:p-8 p-6">
                <div className='xl:text-[80px] md:text-6xl text-4xl w-full font-Italiana text-left leading-[104%]'>It’s easy!</div>
                <div className='xl:text-[48px] md:text-4xl text-3xl ml-8 md:ml-32 xl:ml-48 font-Rockness text-beigePrimary'> Get in Touch with us</div>
            </div>
            <div className="grid grid-cols-2 grid-rows-3 w-full sm:px-12 px-6 gap-x-6">
                <div className="flex flex-col justify-start">
                    <label className="sm:text-[16px] text-sm font-Questrial">First Name</label>
                    <input type="text" className="border-b-2 border-black w-full bg-transparent mx-auto" />
                </div>
                <div className="flex flex-col justify-start">
                    <label className="sm:text-[16px] text-sm font-Questrial">Email Address</label>
                    <input type="text" className="border-b-2 border-black w-full  bg-transparent mx-auto" />
                </div>
                <div className="flex flex-col justify-start col-span-2">
                    <label className="sm:text-[16px] text-sm font-Questrial">Request Type</label>
                    <input type="text" className="border-b-2 border-black w-full  bg-transparent mx-auto" />
                </div>
                <div className="flex flex-col justify-start col-span-2">
                    <label className="sm:text-[16px] text-sm font-Questrial">Briefly tell us about your sore points..</label>
                    <textarea className="border-b-2 border-black w-full  bg-transparent mx-auto" />
                </div>
            </div>
            <div className="w-full sm:justify-start justify-center flex px-12 my-6">
                <PrimaryButton text="Send Us" />
            </div>
        </div>
        <div className="w-1/2 sm:flex hidden flex-col items-center justify-end">
            <img src={git2} alt="git2" className="w-" />
        </div>
    </div>;
}
