import texture2 from "../assets/texture-2.png";
import featured from "../assets/featuredIn.svg";
import yahoo from "../assets/yahoo.svg";
import forbes from "../assets/forbes.svg";
import entrepreneur from "../assets/entrep.svg";
import thrive from "../assets/thrive.svg";

export function Brands() {
    return <div className="w-full relative bg-customWhite pb-32">
        <img src={texture2} alt="texture2" className="w-full h-full absolute z-0 top-0" />
        <div className="w-full h-full z-10 grid sm:grid-rows-1 grid-rows-2 grid-cols-2 sm:grid-cols-5 items-end sm:space-x-8 space-x-1 sm:px-[7%] px-[0px] pt-96 justify-items-center ">
            <img src={featured} alt="featured" className="z-40  sm:block hidden" />
            <img src={yahoo} alt="yahoo" className="z-40  sm:w-2/3 w-1/2" />
            <img src={entrepreneur} alt="entrepreneur" className="z-40   sm:w-2/3 w-1/2" />
            <img src={thrive} alt="thrive" className="z-40   sm:w-2/3 w-1/2" />
            <img src={forbes} alt="forbes" className="z-40 sm:w-2/3 w-1/2 " />
        </div>
    </div>;
}
