import React from 'react';
import { slide as Menu } from 'react-burger-menu'
import hire from "../assets/hire.svg";
const Navbar = () => {
  return (
    <nav className="sm:px-[7%] w-full px-7 py-[40px] flex items-center justify-between font-Questrial">
      <div className="text-[36px] font-Rockness text-[#4E3000] sm:w-1/3">Shivani Mallik</div>
      <div className="space-x-8 sm:flex right-0 items-center justify-end sm:w-2/3 hidden">
        <a className="text-18" href=''>About</a>
        <a className="text-18" href=''>Services</a>
        <a className="text-18" href=''>Testimonials</a>
        <a className="text-18" href=''>Know More</a>
        <button class="btn3 px-10 py-2 relative border bg-black tracking-wider leading-none overflow-hidden text-white" type="button" style={{ "box-shadow": "9.90698px 10.7326px 3.30233px rgba(0, 0, 0, 0.25);" }}>
          <span class="absolute inset-0" style={{ "background": "linear-gradient(86.58deg, #B67D3B 0.53%, #DBA159 28.13%, #B67D3B 65.88%)", "box-shadow": "9.90698px 10.7326px 3.30233px rgba(0, 0, 0, 0.25);" }}></span>
          <span class="absolute inset-0 flex justify-center items-center">
            <div className="flex space-x-2 items-center">
              <p className=" font-Questrial">Work with me</p>
              <img src={hire} alt="hire" className="w-8 h-6" />
            </div>
          </span>
          <div className="flex space-x-2 items-center">
            <p className=" font-Questrial">Work with me</p>
            <img src={hire} alt="hire" className="w-8 h-6" />
          </div>
        </button>
      </div>
      <Menu right className="sm:hidden sm:w-0 text-black w-1/2 ">
        <a className="text-18" href=''>About</a>
        <a className="text-18" href=''>Services</a>
        <a className="text-18" href=''>Testimonials</a>
        <a className="text-18" href=''>Know More</a>
        <a className="text-18 bg-black text-white px-7 py-4" href=''>
          Work with me
        </a>
      </Menu>
    </nav>
  );
};

export default Navbar;
