import smiling from "../assets/smiling.png";
import ellipse from "../assets/ellipse.svg";
import PrimaryButton from "../components/PrimaryButton";

export function HeroComponent() {
    return <div className="flex sm:flex-row flex-col-reverse  sm:px-[7%] px-[28px] items-start justify-between font-Questrial">
        <div className="flex relative xl:w-2/5 md:w-1/2">
            <img src={ellipse} alt="ellipse" className="w-1/3 absolute -top-[] left-0" />
            <div className="md:mt-20 -mt-12 sm:ml-20">
                <p className="xl:text-7xl md:text-6xl text-5xl sm:text-left text-center leading-[114.5%]  font-Italiana"><span className="sm:text-black text-white">Sell with  ease</span> and scale with simplicity</p>
                <p className="text-beigePrimary xl:text-5xl text-4xl sm:text-left text-center leading-[155%] font-Rockness">
                    I will show you how.
                </p>
                {/* black button with white text */}
                <div className="flex w-full sm:justify-start my-2 justify-center">
                    <PrimaryButton text="Hire Me" />
                </div>
            </div>
        </div>
        <img src={smiling} alt="smiling" className="sm:w-2/5" />
    </div>;
}
