import texture2 from "../assets/texture-2.png";
import Slider from "react-slick";
import slide from "../assets/slide.png";
import { useState } from "react";

export function PastHistory() {
    const settings = {
        className: "sm:mx-8 mx-auto sm:w-2/3 w-full",
        centerMode: true,
        infinite: true,
        centerPadding: 0,
        slidesToShow: 3,
        draggable: true,
        focusOnSelect: true,
        swipeToSlide: true,
        speed: 500,
        autoplay: true,
        arrows: false,
        autoplaySpeed: 2000,
        beforeChange: (current, next) => {
            setCardIndex(next);

        },
    };
    const [cardIndex, setCardIndex] = useState(0);
    return <div className="w-full relative bg-customWhite ">
        <img src={texture2} alt="texture2" className="w-full h-full absolute z-0 top-0" />
        <div className="w-full h-full z-10 flex flex-col items-start sm:px-[7%] px-[28px] pt-24 justify-between">
            <div className=" w-11/12 mx-auto z-40">
                <p className="text-[16px] leading-[155%] font-Questrial">
                    Hundreds of students have graduated from my online programs and went on to create massive results in their lives. Those who work with me end up gaining clarity around their purpose, becoming the happiest version of themselves, creating deeper connection, getting paid for their gifts, living a purpose-driven fulfilled life that they love, and so much more.
                    <span className=" font-Rockness text-beigePrimary text-2xl sm:text-[32px] ml-40 block">
                        - Shivani Malik
                    </span>
                </p>
            </div>
        </div>
        <div className="w-full flex justify-center my-16">
            <Slider {...settings}>
                {[1, 2, 3, 4, 5].map((comic, index) => {
                    return (
                        <div className={`transform transition-transform duration-300 w-1/3 h-full ${index === cardIndex ? " scale-110 opacity-100" : " scale-75 opacity-50"}`}>
                            <img src={slide} alt="cover" className=" h-full w-full mx-0 my-auto" />
                        </div>

                    );
                })}

            </Slider>
        </div>
        <div className="sm:w-1/2 mx-auto z-50 relative">
            <p className="sm:text-[28px] font-Questrial z-50 text-center">I was born in Delhi, India
                <span className=" ml-4 text-beigePrimary z-50 font-Rockness text-center sm:text-[28px]">
                    1982
                </span>
            </p>
        </div>
        <div className="w-2/3 text-center mx-auto font-Questrial z-50 relative my-6 sm:visible invisible">
            Lorem ipsum dolor sit amet consectetur. Vulputate risus risus amet a et tincidunt. Eget eget penatibus at ac semper leo cursus. Metus maecenas consequat at arcu sit pretium a nibh dictum.
        </div>
    </div>;
}
