import gradImage from "../assets/gradImage.png";
import ellipseStories from "../assets/ellipseStories.svg";

export function SuccessStories() {
    return <div className="w-full bg-cover z-20 relative h-auto flex justify-center" style={{ backgroundImage: `url(${gradImage})` }}>
        <img src={gradImage} alt="gradImage" className="w-full h-full invisible" />
        <div className="w-full h-full absolute flex justify-center items-end sm:pt-0 sm:pb-16 pb-4">
            <div className="flex flex-col sm:w-3/5 ">
                <div className="flex relative sm:justify-center w-full">
                    <img src={ellipseStories} alt="ellipseList" className="w-1/4 absolute xl:left-36 md:left-12 left-4 xl:mt-0 md:mt-4 -mt-10" />
                    <p className="sm:text-[72px] text-4xl sm:ml-20 text-center sm:mx-0 mx-auto leading-[105%] text-white sm:mt-28 font-Italiana">
                        Success Stories
                    </p>
                </div>
                <div className="flex justify-center w-full mt-10">
                    <p className="xl:text-[32px] md:text-2xl text-white text-center leading-[155%] font-Questrial sm:font-light">
                        Lorem ipsum dolor sit amet consectetur. Vulputate risus risus amet a et tincidunt
                    </p>
                </div>
            </div>

        </div>
    </div>;
}
