import testimonial1 from "../assets/testimonial1.png";
import testimonial2 from "../assets/testimonial2.png";
import testimonial3 from "../assets/testimonial3.png";
import commas from "../assets/commas.svg";

export function Testimonials() {
    return <div className="w-full flex sm:flex-row flex-col justify-between items-center sm:px-[7%] px-[28px] xl:space-x-20 md:space-x-8 pb-10 sm:space-y-0 space-y-6">
        <TestimonialCard name="Ligula Condimentum" designation="CEO, XYZ" description="Lorem ipsum dolor sit amet consectetur. Vulputate risus risus amet a et tincidunt. Eget eget penatibus at ac semper leo cursus. Metus maecenas consequat at arcu sit pretium a nibh dictum. Neque aliquam amet curabitur pretium. Fermentum diam urna neque in interdum amet magna euismod. Fermentum bibendum ullamcorper tortor quis risus arcu sit lorem. Quam odio arcu turpis nisi cursus egestas blandit elementum blandit. Gravida vitae justo lacus mi. Elit vestibulum elementum morbi" image={testimonial1} />
        <TestimonialCard name="Ligula Condimentum" designation="CEO, XYZ" description="Lorem ipsum dolor sit amet consectetur. Vulputate risus risus amet a et tincidunt. Eget eget penatibus at ac semper leo cursus. Metus maecenas consequat at arcu sit pretium a nibh dictum. Neque aliquam amet curabitur pretium. Fermentum diam urna neque in interdum amet magna euismod. Fermentum bibendum ullamcorper tortor quis risus arcu sit lorem. Quam odio arcu turpis nisi cursus egestas blandit elementum blandit. Gravida vitae justo lacus mi. Elit vestibulum elementum morbi" image={testimonial2} />
        <TestimonialCard name="Ligula Condimentum" designation="CEO, XYZ" description="Lorem ipsum dolor sit amet consectetur. Vulputate risus risus amet a et tincidunt. Eget eget penatibus at ac semper leo cursus. Metus maecenas consequat at arcu sit pretium a nibh dictum. Neque aliquam amet curabitur pretium. Fermentum diam urna neque in interdum amet magna euismod. Fermentum bibendum ullamcorper tortor quis risus arcu sit lorem. Quam odio arcu turpis nisi cursus egestas blandit elementum blandit. Gravida vitae justo lacus mi. Elit vestibulum elementum morbi" image={testimonial3} />
    </div>;
}
function TestimonialCard({ name, designation, description, image }) {
    return (
        <div className=" aspect-square w-full  relative flex flex-col bg-customWhite items-center justify-center xl:pb-10 md:pb-3">
            <img src={texture2} className="w-full h-full absolute bg-customWhite top-0" />
            <div className="w-full h-full z-10 flex flex-col items-center justify-center">
                <div className="w-full xl:px-6 md:px-3 xl:pt-4 xl:pb-4 md:pb-2 md:pt-2">
                    <img src={commas} alt="commas" className="xl:w-1/4 md:w-1/5" />
                </div>
                <div className="custom-scroll h-40 overflow-y-scroll xl:mx-8 md:mx-4 text-justify pr-6 xl:mb-6 md:mb-3 font-Questrial">
                    {description}
                </div>
                <div className="w-full flex xl:px-6 md:px-3 items-center justify-between space-x-5">
                    <img src={image} alt="" className="xl:w-1/4 md:w-1/5" />
                    <div className="flex flex-col items-start justify-start w-3/4">
                        <div className="text-[16px] xl:leading-[155%] font-Questrial">ligula condimentum. <p>Vestibulum ante</p> </div>
                        <div className="xl:text-[24px] md:text-base text-xl font-Italiana">{name}</div>
                    </div>
                </div>
            </div>

        </div>
    );
}
